.topics {
    padding-top: 5%;
    padding-bottom: 5%;
    padding-left: 10%;
    padding-right: 10%;
    background: lightslategrey;
    height: auto;
}
.topicsDiv {
  overflow: hidden; /* 清除浮动 */
  width: 100%;
  height: auto;
}
.grid4-container {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    ap: 20px;
}
.grid4-itemTotal{
  position: relative;
}
.topicsUp{
  font-size: 28px;
  font-weight: bold;
}
.topicsDivDown{
    flex: 1 0 auto;
    display:flex;
    justify-content: center;
    align-items:flex-end;
    padding-bottom:20px;
}

@media only screen and (min-width: 1200px) {
}

@media only screen and (min-width: 1024px) {
}

@media only screen and (max-width: 1024px) {
  .topicsUp{
    margin-left:0%;
  }
}

@media screen and (max-width: 960px) {
  .topicsUp{
    margin-left:0%;
  }
  .cards__items {
    display: block;
  }
  .grid4-container{
      display: block;
  }
}
